@media (min-width: 1360px) {
  section.section-welcome {
    .wrapper-inner {
      min-height: 700px;
    }
  }

  section.section-offer {
    .wrapper-inner {
      min-height: 700px;
    }
  }
}

// Small devices (landscape phones, less than 768px)
@media (max-width: 767.98px) {
  nav.section-header {
    background-color: #f9f9f9;

    .button-wrapper {
      position: absolute;
      z-index: 1;
      top: 0;
      right: 15px;
      display: flex;
      justify-content: flex-end;

      .nav-button {
        display: flex;
      }
    }

    .header-flexbox-wrapper {
      background-color: transparent;

      .header-flexbox {
        justify-content: center;
        padding: 0;

        .flexbox-nav-wrapper {
          position: fixed;
          top: 0;
          left: 0;
          height: 100%;
          width: 100%;
          overflow-y: scroll;
          background: @color1;
          z-index: 555;
          opacity: 0;
          visibility: hidden;
          transition: all 0.3s;

          .nav-button-close {
            display: block;
          }

          .flexbox-nav-inner {
            display: flex;
            flex-direction: column;
            justify-content: center;
            min-height: 100%;
            padding: 50px;
            margin-right: 0;

            .flexbox-nav {
              flex-direction: column;

              li {
                padding: 15px 0;

                &.active {
                  a {
                    background-color: transparent;
                  }
                }

                a {
                  color: #000;
                  padding: 0;
                  font-size: 16px;
                }
              }
            }
          }

          &.active {
            opacity: 1;
            visibility: visible;
          }
        }
      }
    }
  }
}

