.btn {
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  transition: all 850ms;
  backface-visibility: hidden;
  border-radius: 0;
}
.btn.btn-default {
  padding: 15px 20px;
  font-size: 14px;
  line-height: 14px;
  font-weight: 600;
  font-family: "tt_commonsmedium";
  letter-spacing: 2.5px;
  text-transform: uppercase;
  color: #212825;
  transition: background-color 350ms;
  background-image: linear-gradient(to right, #f09819 0%, #edde5d 40%, #f09819 100%);
  transition: 0.5s;
  background-size: 200% auto;
}
.btn:hover.btn-default {
  background-position: right center;
}
.btn:focus,
.btn:active {
  outline: none;
  box-shadow: none;
}
.nav-button {
  padding: 10px 0;
  border: none;
  background: none;
  display: flex;
  align-items: center;
  outline: none;
}
.nav-button .nav-button-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}
.nav-button .button-bar {
  height: 1.5px;
  width: 24px;
  border-radius: 10px;
  transition: all 0.5s;
  background-color: #000;
}
.nav-button .button-bar + .button-bar {
  margin-top: 5px;
}
.nav-button:focus,
.nav-button:active {
  outline: none;
  box-shadow: none;
}
.nav-button-close {
  position: absolute;
  z-index: 11;
  width: 100%;
  top: 40px;
  left: 0;
}
.nav-button-close .container {
  display: flex;
  justify-content: flex-end;
}
.nav-button-close .container:before,
.nav-button-close .container:after {
  content: none;
}
.nav-button-close button {
  outline: none;
  border: none;
  background: none;
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-button-close button .button-bar {
  display: block;
  position: absolute;
  width: 20px;
  height: 1px;
  background: #222;
}
.nav-button-close button .button-bar:nth-child(1) {
  transform: rotate(45deg);
}
.nav-button-close button .button-bar:nth-child(2) {
  transform: rotate(-45deg);
}
#return-to-top {
  position: fixed;
  z-index: 333;
  bottom: 80px;
  right: 0;
  display: block;
  text-decoration: none;
  visibility: hidden;
  opacity: 0;
  transition: all 350ms ease;
  cursor: pointer;
  transform: translateY(100px) rotate(90deg);
}
#return-to-top.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0) rotate(90deg);
}
#return-to-top.white .line {
  background-color: #eeeae7;
}
#return-to-top.white p {
  color: #fff;
}
#return-to-top.white:hover p {
  color: #fff;
}
#return-to-top .line {
  width: 120px;
  height: 0.5px;
  background-color: #dbae97;
}
#return-to-top p {
  font-size: 15px;
  color: #000;
  text-transform: uppercase;
  text-align: right;
  margin-right: 3px;
  transition: color 350ms;
  font-weight: 600;
}
#return-to-top:hover p {
  color: #dbae97;
}
@font-face {
  font-family: 'Tenor Sans';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/tenorsans/v15/bx6ANxqUneKx06UkIXISr3I.ttf) format('truetype');
}
@font-face {
  font-family: "tt_commonsdemibold";
  src: url("../fonts/ttcommons-demibold-webfont.woff2") format("woff2"), url("../fonts/ttcommons-demibold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "tt_commonslight";
  src: url("../fonts/ttcommons-light-webfont.woff2") format("woff2"), url("../fonts/ttcommons-light-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "tt_commonsmedium";
  src: url("../fonts/ttcommons-medium-webfont.woff2") format("woff2"), url("../fonts/ttcommons-medium-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "tt_commonsregular";
  src: url("../fonts/ttcommons-regular-webfont.woff2") format("woff2"), url("../fonts/ttcommons-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
body {
  min-height: 100vh;
  height: 100%;
  font-family: "tt_commonsregular";
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: background-color 350ms;
  background-image: url(../img/backgrounds/marbled-background.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
}
body.no-scroll {
  overflow: hidden;
  max-height: 100vh;
}
body ul,
body ol,
body p,
body h1,
body h2,
body h3,
body h4,
body h5,
body h6 {
  margin: 0;
  padding: 0;
}
body a {
  text-decoration: none !important;
}
.bg-border-gold {
  position: absolute;
  z-index: -1;
  width: 70%;
  height: 70%;
  background-image: radial-gradient(ellipse farthest-corner at right bottom, #f0df58 0, #eecc35 21%, #f6e27a 30%, #fff 40%, #fff 45%, #eecc35 70%, #f0df58 100%);
}
.alert-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.2);
  z-index: 555;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s;
}
.alert-wrapper .alert-service {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  transform: translateX(8px);
  padding: 50px 0;
}
.alert-wrapper .alert-service .service-inner {
  padding: 50px;
  background: #fff;
  width: 100%;
  box-shadow: 0px 0px 32px 1px rgba(0, 0, 0, 0.15);
  text-align: center;
  border-top: 5px solid #fff;
  position: relative;
  transform: scale(0.8);
  transition: all 0.5s;
}
.alert-wrapper .alert-service .service-inner .inner-dismiss {
  top: 10px;
  right: 10px;
  position: absolute;
  border: 0;
  background: none;
  padding: 0;
  cursor: pointer;
  display: flex;
  padding: 5px;
  outline: none;
}
.alert-wrapper .alert-service .service-inner .inner-dismiss img {
  height: 15px;
}
.alert-wrapper .alert-service .service-inner.success {
  border-top: 5px solid #78b042;
}
.alert-wrapper .alert-service .service-inner.warning {
  border-top: 5px solid #d51a27;
}
.alert-wrapper .alert-service .service-inner.info {
  border-top: 5px solid #1a88d5;
}
.alert-wrapper.active {
  opacity: 1;
  visibility: visible;
}
.alert-wrapper.active .alert-service .service-inner {
  transform: none;
}
.modal-service-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 666;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
  overflow-x: scroll;
}
.modal-service-wrapper.active {
  opacity: 1;
  visibility: visible;
}
.modal-service-wrapper .modal-service {
  padding: 50px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: transform 0.4s;
}
.modal-service-wrapper .modal-service.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(-40px);
}
.modal-service-wrapper .modal-service .service-inner {
  position: relative;
  background: #fff;
  min-width: 900px;
  max-width: 60%;
  padding: 50px 25px 25px 25px;
}
.modal-service-wrapper .modal-service .service-inner .service-button {
  position: absolute;
  top: 20px;
  right: 20px;
  border: none;
  background: none;
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-service-wrapper .modal-service .service-inner .service-button .button-bar {
  display: block;
  position: absolute;
  width: 20px;
  height: 1px;
  background: #222;
}
.modal-service-wrapper .modal-service .service-inner .service-button .button-bar:nth-child(1) {
  transform: rotate(45deg);
}
.modal-service-wrapper .modal-service .service-inner .service-button .button-bar:nth-child(2) {
  transform: rotate(-45deg);
}
.modal-service-wrapper .modal-service .service-inner .service-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 50px;
}
.modal-service-wrapper .modal-service .service-inner .service-content .heading {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 20px;
}
.modal-service-wrapper .modal-service .service-inner .service-content .btn {
  display: flex;
  justify-content: center;
  margin-top: 5px;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul {
  list-style-type: none;
  width: 25%;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li:not(.heading) {
  font-size: 16px;
  position: relative;
  padding: 10px 0 10px 15px;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li:not(.heading):before {
  content: "";
  position: absolute;
  top: 25px;
  left: 0px;
  width: 5px;
  height: 5px;
  border-radius: 100px;
  background-color: #dbae97;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li.heading {
  font-size: 20px;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li strong {
  display: block;
  font-size: 18px;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li p {
  margin-top: 5px;
  padding-top: 5px;
  border-top: 1px solid #dbae97;
  font-weight: 700;
}
.modal-service-wrapper .modal-service .service-inner .service-content ul li .btn-default {
  padding: 5px 15px;
  font-size: 12px;
}
.modal-service-wrapper .modal-service .service-inner .service-content .hidden-element {
  width: 25%;
  visibility: hidden;
  opacity: 0;
}
.modal-service-wrapper .modal-service .service-inner .service-buttons {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.modal-service-wrapper .modal-service .service-inner .service-buttons .btn-link {
  padding: 0;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
}
.select-wrapper {
  position: relative;
}
.select-wrapper label {
  color: #666;
}
.select-wrapper select {
  cursor: pointer;
  padding: 7px 100px 5px 10px;
  border: 1px solid #dbae97;
  border-radius: 4px;
  background: #fff;
  height: 31px;
  -moz-appearance: none;
  -webkit-appearance: none;
  font-size: 13px;
  appearance: none;
  outline: none;
  display: block;
  color: #666;
  width: 100%;
}
.select-wrapper:after {
  cursor: pointer;
  content: "";
  height: 10px;
  width: 10px;
  background: url("../img/pictures/down-chevron.svg") center;
  background-size: cover;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
.checkbox-wrapper {
  cursor: pointer;
  display: block;
  margin-bottom: 0;
}
.checkbox-wrapper .checkbox-box {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 15px;
  text-transform: none;
  color: #777;
  text-align: left;
  transition: all 0.3s;
}
.checkbox-wrapper .checkbox-box:before {
  content: "";
  min-width: 16px;
  max-width: 16px;
  height: 16px;
  display: block;
  background: url("../img/pictures/checkbox.svg") no-repeat;
  margin-right: 10px;
  transition: all 0.3s;
}
.checkbox-wrapper input {
  position: absolute;
  opacity: 0;
  width: auto !important;
}
.checkbox-wrapper > input:checked + .checkbox-box {
  font-weight: 600;
  color: #eeeae7;
}
.checkbox-wrapper > input:checked + .checkbox-box:before {
  background: url("../img/pictures/checkbox-checked.svg") no-repeat;
}
#return-to-top {
  position: fixed;
  z-index: 333;
  height: 45px;
  width: 45px;
  right: 50px;
  bottom: 50px;
  border-radius: 100px;
  border: 1px solid #eeeae7;
  background-color: #dbae97;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  visibility: hidden;
  opacity: 0;
  transition: all 350ms ease;
  cursor: pointer;
  transform: translateY(50px);
}
#return-to-top svg {
  fill: #000;
  height: 13px;
  width: 13px;
  transform: rotate(-90deg);
}
#return-to-top:hover {
  background: #ce9071;
}
#return-to-top.active {
  opacity: 1;
  visibility: visible;
  transform: none;
}
nav.section-header {
